import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Road from 'components/Road/Road'
import Broadcast from 'components/Broadcast/Broadcast'
Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        redirect: '/Road'
    }, {
        path: '/Road',
        component: Road
    }, {
        path: '/Broadcast',
        component: Broadcast
    }]
})


